.slider-body {
  height: 100%;
  width: 100%;
  position: absolute;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

.item {
  display: block;
  position: absolute;
  width: 250px;
  max-height: 500px;
  text-align: center;
  color: #fff;
  opacity: 0.4;
  -webkit-transition: -webkit-transform .3s ease-in;
  transition: -webkit-transform .3s ease-in;
  -o-transition: transform .3s ease-in;
  transition: transform .3s ease-in;
  transition: transform .3s ease-in, -webkit-transform .3s ease-in;
}

.item img {
  max-height: 500px;
}

@media (max-width: 767.98px) {
  .item img {
    max-height: 300px;
  }
}

.item-prev {
  -webkit-transform: rotateY(0deg) translateX(-220px);
  transform: rotateY(0deg) translateX(-220px);
  opacity: 1;
}

@media (max-width: 991.98px) {
  .item-prev {
    -webkit-transform: rotateY(0) translateX(-190px);
    transform: rotateY(0) translateX(-190px);
  }
}

@media (max-width: 767.98px) {
  .item-prev {
    -webkit-transform: rotateY(0) translateX(-75px);
    transform: rotateY(0) translateX(-75px);
